/* кнопка героя и регистрации */
$time: 1000ms;

$btns: (
  alpha: (
    gstart: #ff6b09,
    gend: #ffffff,
    tstart: #ffffff,
    tend: #ff6b09,
  ),
);

.button-registration {
  position: relative;
  overflow: hidden;

  display: flex;
  width: 260px;
  height: 48px;
  font-family: $primary-font;
  font-style: normal;
  @include font(700, 12px, 15px);
  letter-spacing: 0.08em;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  color: $third-color;
  background-color: $accent-color;
  border: none;
  cursor: pointer;
  /* transition: background-color 250ms $timing-function; */
  transition: all $time;

  // &.header-buttons {
  //   background-color: transparent;
  //   color: $accent-color;
  //   outline: 2px solid $accent-color;
  // }
  .btn-reg {
    position: relative;
    z-index: 1;
  }

  &:before {
    content: '';
    background-color: #ffffff;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    transform: scale(4) translateX(-100%);
    transition: all $time * 1.5 ease-out;
  }

  &:hover,
  &:focus,
  &:active {
    &:before {
      transform: scale(4) translate(37%);
    }
    border: 2px solid $accent-color;
  }

  /*  &:hover {
    color: $accent-color;
    background-color: $third-color;
    border: 2px solid $accent-color;
  } */
}

@each $bname, $bcolors in $btns {
  .btn--#{$bname} {
    background-color: map-get($bcolors, gstart);
    color: map-get($bcolors, tstart);

    &:before {
      background-color: map-get($bcolors, gend);
      background-image: linear-gradient(
        to right,
        map-get($bcolors, gend) 30%,
        map-get($bcolors, gstart) 100%
      );
      position: absolute;
    }

    &:hover,
    &:focus,
    &:active {
      color: map-get($bcolors, tend);
    }
  }
}

